/*================================================
Default CSS
=================================================*/
:root {
  scroll-behavior: initial;
}

body {
  font-family: "Poppins", sans-serif;
  padding: 0;
  margin: 0;
  font-size: 15px;
}

a {
  display: inline-block;
  transition: 0.5s;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.ptb-120 {
  padding-top: 120px;
  padding-bottom: 120px;
}

img {
  max-width: 100%;
}

p {
  line-height: 1.9;
  color: #707070;
  margin-bottom: 15px;
  font-size: 15px;
}

p:last-child {
  margin-bottom: 0;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  color: #0d1028;
}

.text-right {
  text-align: end;
}

.text-left {
  text-align: start;
}

.form-group {
  margin-bottom: 30px;
}

textarea.form-control {
  min-height: auto !important;
  height: auto;
}

label {
  margin-bottom: 10px;
}

/*btn btn-primary*/
.btn {
  font-weight: 600;
  font-size: 15px;
  color: #ffffff;
  box-shadow: 0 8px 6px -6px rgba(50, 51, 51, 0.4);
  border: none;
  padding: 15px 30px;
  border-radius: 0;
  transition: 0.5s;
}

.btn-primary {
  color: #ffffff;
  background: #0d1028;
  position: relative;
  z-index: 1;
}

.btn-primary::before, .btn-primary::after {
  content: '';
  position: absolute;
  top: 0;
  width: 51%;
  background: #FF2D55;
  height: 100%;
  z-index: -1;
  transition: 0.5s;
}

.btn-primary::before {
  left: 0;
}

.btn-primary::after {
  right: 0;
}

.btn-primary:hover::before, .btn-primary:hover::after, .btn-primary:focus::before, .btn-primary:focus::after {
  width: 0;
}

.btn-primary.focus, .btn-primary:focus {
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
  color: #ffffff;
}

.btn-primary:hover, .btn-primary:focus {
  background: #0d1028;
  color: #ffffff;
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus {
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active {
  background: #0d1028;
  color: #ffffff;
}

.btn-primary.disabled, .btn-primary:disabled {
  opacity: 1;
  background: #0d1028;
}

.btn-secondary {
  color: #ffffff;
  background: #0d1028;
  position: relative;
  z-index: 1;
}

.btn-secondary::before, .btn-secondary::after {
  content: '';
  position: absolute;
  top: 0;
  width: 51%;
  background: #00acee;
  height: 100%;
  z-index: -1;
  transition: 0.5s;
}

.btn-secondary::before {
  left: 0;
}

.btn-secondary::after {
  right: 0;
}

.btn-secondary:hover::before, .btn-secondary:hover::after, .btn-secondary:focus::before, .btn-secondary:focus::after {
  width: 0;
}

.btn-secondary.focus, .btn-secondary:focus {
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
  color: #ffffff;
}

.btn-secondary:hover, .btn-secondary:focus {
  background: #0d1028;
  color: #ffffff;
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
}

.btn-secondary:not(:disabled):not(.disabled).active:focus, .btn-secondary:not(:disabled):not(.disabled):active:focus {
  box-shadow: 0 13px 27px 0 rgba(245, 78, 162, 0.25);
}

.btn-secondary:not(:disabled):not(.disabled).active, .btn-secondary:not(:disabled):not(.disabled):active {
  background: #0d1028;
  color: #ffffff;
}

.btn-secondary.disabled, .btn-secondary:disabled {
  opacity: 1;
  background: #0d1028;
}

/*section-title*/
.section-title {
  position: relative;
  margin-bottom: 60px;
  z-index: 1;
}

.section-title span {
  color: #FF2D55;
  letter-spacing: 1px;
  font-size: 16px;
  text-transform: uppercase;
  display: block;
}

.section-title h2 {
  font-size: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.section-title h2 b {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 80%, #FF2D55 80%);
  font-weight: unset;
  background-size: 100px 1.07em !important;
}

.section-title .btn {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -18px;
}

.section-title .bg-title {
  color: rgba(73, 57, 231, 0.05);
  font-weight: 700;
  font-size: 140px;
  line-height: 120px;
  position: absolute;
  top: 0;
  right: 35%;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: -1;
  margin-top: -25px;
}

.section-title .bar {
  position: relative;
  width: 20px;
  height: 4px;
  background: #FF2D55;
  display: block;
}

.section-title .bar::after, .section-title .bar::before {
  position: absolute;
  height: 100%;
  top: 0;
  right: 0;
  content: '';
}

.section-title .bar::after {
  width: 25px;
  background: #00acee;
  right: -29px;
}

.section-title .bar::before {
  background: #efa506;
  width: 30px;
  right: -63px;
}

/*owl-carousel-btn*/
.owl-theme .owl-nav.disabled + .owl-dots {
  margin-top: 0;
  line-height: .01;
}

.owl-theme .owl-dots .owl-dot span {
  width: 15px;
  height: 15px;
  margin: 0 4px;
  background: transparent;
  border: 1px solid #dfdfdf;
  display: block;
  transition: 0.5s;
  border-radius: 50%;
  position: relative;
}

.owl-theme .owl-dots .owl-dot:hover, .owl-theme .owl-dots .owl-dot:focus {
  outline: 0;
  box-shadow: unset !important;
}

.owl-theme .owl-dots .owl-dot:hover span, .owl-theme .owl-dots .owl-dot:focus span {
  background: #00acee;
  border-color: #00acee;
}

.owl-theme .owl-dots .owl-dot.active span {
  background: #FF2D55;
  border-color: #FF2D55;
}

/*form-control*/
.form-control {
  height: 50px;
  padding: 15px;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid #eeeeee;
  border-radius: 0;
  transition: 0.5s;
}

.form-control:hover, .form-control:focus, .form-control.active {
  box-shadow: unset;
  border-color: #FF2D55;
}

/*================================================
Preloader CSS
=================================================*/
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999999;
  overflow: hidden;
  background: #0d1028;
}

.preloader .loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 110px;
  height: 110px;
  margin: -80px 0 0 -75px;
  border-radius: 50%;
  border: 7px solid transparent;
  border-top-color: #FF2D55;
  -webkit-animation: spin 1.7s linear infinite;
          animation: spin 1.7s linear infinite;
  z-index: 11;
}

.preloader .loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 7px solid transparent;
  border-top-color: #efa506;
  -webkit-animation: spin-reverse .6s linear infinite;
          animation: spin-reverse .6s linear infinite;
}

.preloader .loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 7px solid transparent;
  border-top-color: #00acee;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin-reverse {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin-reverse {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

