Navbar CSS
=================================================*/
.elkevent-mobile-nav {
  display: none;
}

.elkevent-mobile-nav.mean-container .mean-nav ul li a.active {
  color: #FF2D55;
}

.elkevent-nav {
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 20px;
  width: 100%;
  z-index: 999;
  height: auto;
}

.elkevent-nav nav ul {
  padding: 0;
