}

.elkevent-nav nav .navbar-nav .nav-item a:hover, .elkevent-nav nav .navbar-nav .nav-item a:focus, .elkevent-nav nav .navbar-nav .nav-item a.active {
  color: #FF2D55;
}

.elkevent-nav nav .navbar-nav .nav-item:hover a {
